    box-shadow: 0 0 0 4px rgba(var(--gold-rgb), 0.55), 0 0 30px rgba(var(--gold-rgb), 0.5);
    transform: translateY(-4px);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(var(--gold-rgb), 0.4), 0 0 18px rgba(var(--gold-rgb), 0.3);
